import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { DetailCategoryComponent } from './detail-category.component';
import { RouterModule, Routes } from '@angular/router';
import { TranslateModule } from '@ngx-translate/core';
import { NgZorroAntModule } from 'src/app/ng-zorro-ant.module';
import { DetailProductComponent } from './detail-product/detail-product.component';
import { IonicModule } from '@ionic/angular';
import { VhComponent } from 'src/app/sales/components/vh-component/vh-component';
import { BillDetailModule } from 'src/app/sales/components/bill-detail/bill-detail.module';
const routes: Routes = [
  {
    path: '',
    component: DetailCategoryComponent
  },
  {
    path: 'detail-product',
    component: DetailProductComponent
  },

]

@NgModule({
  declarations: [DetailCategoryComponent, DetailProductComponent, ],
  imports: [
    CommonModule,
    RouterModule.forChild(routes),
    TranslateModule,
    NgZorroAntModule,
    IonicModule,
    BillDetailModule
  ],
  providers :[VhComponent]
})
export class DetailCategoryModule { }
